/* src/components/ChatAgent/ChatAgent.css */
.chat-agent {
  display: flex;
  flex-direction: column;
  max-width: 860px;
  height: 95%;
  flex: 1;
  width: 100%;
  margin: auto;
  position: relative;
}

.messages {
  flex: 1 1;
  overflow-y: auto;
  padding: 10px;
  background-color: var(--card-background-color);
  border: 1px solid var(--border-color);
  max-width: 860px;
  margin: auto;
  width: 100%;
  border-top: none;
  border-bottom: none;
}

.message {
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;

}

.message.user {
  background-color: var(--primary-color);
  color: white;
  align-self: flex-end;
}

.message.bot {
  background-color: var(--background-color);
  color: #343a40;
  align-self: flex-start;
}

.input-container {
  display: flex;
  align-items: center;
  border: 1px solid var(--border-color);
  padding: 5px;
  background-color: var(--card-background-color);
  position: absolute;
  bottom: 0;
  width: 100%;
  max-width: 860px;
  box-sizing: border-box;
  left: 50%;
  transform: translateX(-50%);
}

.assistant {
  color: var(--text-color);
}

.input-container textarea {
  flex: 1;
  padding: 10px;
  border: none;
  outline: none;
  resize: none;
  height: 40px;
  background-color: var(--card-background-color);
  color: var(--text-color);
  overflow: hidden; /* Remove scroll bar */
  display: flex;
  align-items: center;
}

.input-container input[type='file'] {
  display: none;
}

.input-container label {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-right: 10px;
}

.input-container .file-label {
  padding: 0 10px;
  border-right: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  color: var(--text-color);
}

.input-container button {
  padding: 10px 20px;
  border: none;
  background-color: var(--primary-color);
  color: white;
  border-radius: 4px;
  cursor: pointer;
}

.input-container button:hover {
  background-color: #0056b3;
}

